import { createSlice } from '@reduxjs/toolkit';

export const filterSlice = createSlice({
    name: 'filter',
    initialState: {
        filter: " ",
    },
    reducers: {
        update(state, action) {
        return action.payload;
        },
    },
});

export const { update } = filterSlice.actions;
export const filterReducer = filterSlice.reducer;